import { graphql } from "gatsby";
import * as React from "react";
import Layout from "../components/Layout";
import PostSummary from "../components/PostSummary";
import { BODY_BACKGROUND_CLASSES } from "../constants";
import useTailwindCssDarkMode from "../hooks/useTailwindCssDarkMode";
import { AllMdx } from "../models/graphql/types";

interface IndexPageProps {
  data: {
    allMdx: AllMdx;
  };
}

const IndexPage = ({ data }: IndexPageProps) => {
  const darkModeState = useTailwindCssDarkMode(BODY_BACKGROUND_CLASSES);

  const allMdx = data.allMdx.nodes;

  return (
    <Layout darkModeState={darkModeState}>
      <main className="">
        <section className="flex justify-center ml-16 mr-16 bg-marigold-300 mb-4">
          <ul className="list-disc">
            남은 작업 목록
            <li>카테고리/시리즈/code 시각화</li>
            <li>google analytics 적용</li>
            <li>웹접근성 추가</li>
            <li>포스팅 아카이브 및 스크립트</li>
            <li>rss</li>
            <li>댓글??? 좋아요???</li>
          </ul>
        </section>
        <section className="flex flex-wrap gap-4 justify-center ml-16 mr-16">
          {allMdx.length > 0 ? (
            allMdx.map((mdx) => (
              <PostSummary key={mdx.id} postPageItem={mdx}></PostSummary>
            ))
          ) : (
            <p>포스트가 없습니다.</p>
          )}
        </section>
      </main>
    </Layout>
  );
};

export const query = graphql`
  query {
    allMdx(sort: { fields: frontmatter___datetime, order: DESC }) {
      nodes {
        frontmatter {
          title
          datetime(formatString: "YYYY년 MM월 DD일")
          hero_image {
            childImageSharp {
              gatsbyImageData(
                width: 300
                aspectRatio: 1
                placeholder: BLURRED
                formats: [AUTO, WEBP]
                quality: 100
                transformOptions: { fit: CONTAIN }
              )
            }
          }
          hero_image_alt
        }
        id
        slug
        fileAbsolutePath
        excerpt(truncate: true, pruneLength: 120)
      }
    }
  }
`;

export default IndexPage;
