import { Link } from "gatsby";
import { GatsbyImage, getImage } from "gatsby-plugin-image";
import React from "react";
import { MdxNode } from "../models/graphql/types";
import * as styles from "./PostSummary.module.css";

interface PostSummaryProps {
  postPageItem: MdxNode;
}

const PostSummary = ({ postPageItem }: PostSummaryProps) => {
  const POST_SLUG_PATH = "post";

  const image = getImage(postPageItem.frontmatter.hero_image);

  return (
    <article className={`${styles.PostSummaryWidth} group cursor-pointer`}>
      <Link to={`${POST_SLUG_PATH}/${postPageItem.slug}`}>
        <figure className="grid grid-rows-[32px_130px_90px_124px] grid-cols-3">
          <p className="row-start-1 row-end-2 col-start-3 col-end-4 z-10 flex justify-center items-center opacity-80 group-hover:opacity-100 text-white p-0.5 bg-black text-xs rounded-tr-xl">
            {postPageItem.frontmatter.datetime}
          </p>
          {image && (
            <GatsbyImage
              className="row-start-1 row-end-5 col-start-1 col-end-4 w-full rounded-xl z-0"
              image={image}
              alt={postPageItem.frontmatter.hero_image_alt}
            />
          )}
          <figcaption className="row-start-4 row-end-5 col-start-1 col-end-4 z-10 opacity-70 text-white p-4 bg-black text-sm rounded-br-xl rounded-bl-xl flex flex-col gap-1 group-hover:opacity-90">
            <h3 className="text-base font-bold line-clamp-1 group-hover:text-marigold-500">
              {postPageItem.frontmatter.title}
            </h3>
            <p className="text-gray-300 p-1 hover:underline hover:underline-offset-1 line-clamp-3">
              {postPageItem.excerpt}
            </p>
          </figcaption>
        </figure>
        <div className="row-start-4 row-end-6 col-start-1 col-end-4">
          {(() => {
            if (postPageItem.frontmatter.series)
              return postPageItem.frontmatter.series;
          })()}
        </div>
      </Link>
    </article>
  );
};

export default PostSummary;
